import '../App.css';
import Movie from '../json/movie.json';
import FeaMov from './FeatureMov';

import { useParams, Link } from "react-router-dom";

function Popup() {

    const {movie} = useParams();

    let trArry =[];
    Movie.Feature.map( (vdo, k) => (
        trArry.push( vdo.trailer )
    ))

    return(
        <>
            <FeaMov/>
            <Link to={`/feature`} className="trailer">
                {
                    Movie.Feature.filter( (pop) => (
                        pop.movie === movie
                    )).map((pop, k) =>(
                        <div key={k} dangerouslySetInnerHTML={{__html : pop.trailer}}></div>
                    ))
                }
                
            </Link>
        </>
    )
}
export default Popup;