import '../App.css';
import Movie from '../json/movie.json';
import ShortMov from './ShortMov';

import { useParams, Link } from "react-router-dom";

function ShortPop() {

    const {movie} = useParams();

    return(
        <>
            <ShortMov/>
            <Link to={`/short`} className="shBg">
                {
                    Movie.Shorts.filter( (pop) => (
                        pop.movie === movie
                    )).map( (data,k) => (
                        <div className="shortInfo" style={{
                            backgroundImage: `url('${process.env.PUBLIC_URL}/img/short/${data['popbg']}')`
                        }} key={k}>
                            <div className="info">
                                <p>
                                    <img src={require(`../img/short/${data['poster']}`).default} alt=""/>
                                </p>
                                <article>
                                    <h2>{data['movie']}</h2>
                                    <p dangerouslySetInnerHTML={{__html : data.detail}}></p>
                                </article>
                                <button to={`/short`}>X</button>
                            </div>
                        </div>
                    ))

                }
                
            </Link>
        </>
    )
}
export default ShortPop;