import React, { useState } from "react";
import '../App.css';
import Movie from '../json/movie.json';

import playBtn from "../img/play.png"
import { Link } from "react-router-dom";


export default function FeatureMov() {
    
    let [ name, setName ] = useState('');
    function movFil(e) {
        setName(e.target.value)
    }
    let [movIdx, setMovIdx] = useState(0)
    function movChange(e) {
        setMovIdx( e.target.id)
    }

    return(
        <div className="feature">
            <div className="searchBar">
                <p>
                    <span/>
                    <input type="search" placeholder="Search" onChange={ movFil}/>
                </p>
            </div>
            <ul className="long">
                {
                    Movie.Feature.filter((val) => {
                            if( name == ""){
                                return val
                            }else if( val['movie'].toLowerCase().includes(name.toLowerCase()) ){
                                return val
                            }
                        }).map( (data, k) => (
                            <li key={k} >
                                <Link to={`/feature/character/${movIdx}`} onMouseOver={movChange} >
                                    <img src={require(`../img/long/${data['poster']}`).default} alt="" id={k}/>
                                </Link>
                                <article>
                                    <span className="mvName">{data['movie']}</span>
                                    <Link to={`/feature/trailer/${data['movie']}`}>
                                        <img src={playBtn} alt=""/>
                                    </Link>
                                </article>
                            </li>
                        ))
                }
            </ul>
        </div>
    )

}