import './App.css';

import Main from "./comp/Main"
import Feature from "./comp/Feature"
import Short from "./comp/Short"
import About from "./comp/About"
import Footer from './comp/Footer';

import Logo from "./img/logo.png"

import {BrowserRouter, Route, Switch, NavLink } from "react-router-dom";



function App() {

  return (
    <>
      <div>
        <BrowserRouter>
        <nav>
          <NavLink exact to="/pixar"><img src={Logo} alt="mainlogo"/></NavLink>
          <div>
            <NavLink to="/feature">FEATURE FILMS</NavLink>
            <NavLink to="/short">SHORT FILMS</NavLink>
            <NavLink to="/about">ABOUT</NavLink>
          </div>
        </nav>
        <Switch>
          <Route exact path="/pixar"><Main/></Route>
          <Route path="/feature"><Feature/></Route>
          <Route path="/short"><Short/></Route>
          <Route path="/about"><About/></Route>
        </Switch>
        </BrowserRouter>
      </div>
      <footer><Footer/></footer>
    </>
  );
}

export default App;
