import React, { useState } from "react";
import '../App.css';
import Movie from '../json/movie.json';
import { useParams } from "react-router-dom";

function Character() {

    const idx = useParams();
    const chaIdx = idx.movie;

    // const current = decodeURI(window.location.href);
    // const search = current.split("/")[4];


    let bgArr =[];
    Movie.Feature.map( (bg, k) => (
        bgArr.push( bg.bgimg )
    ))

    const bgStyle = {
        backgroundImage: `url('${process.env.PUBLIC_URL}/img/long/${bgArr[chaIdx]}')`
    }

    let charArr = [], chaNameArr = [], chaDetailArr = [], imgArr = [];
    Movie.Feature.map( ( ch, k) => ( charArr.push( ch.character ) ))
    
    charArr[chaIdx].map( (name,k) => ( chaNameArr.push(name.name) )) // 캐릭터 이름
    charArr[chaIdx].map( (name,k) => ( chaDetailArr.push(name.detail) )) // 캐릭터 정보
    charArr[chaIdx].map( (indi,k) => ( imgArr.push(indi.imgs) )) // 캐릭터 이미지

    let [ faceIdx, setFaceIdx ] = useState(0);
    function faceChange(e) {
        setFaceIdx(e.target.id)
    }

    function clickBnw(e) {
        const imm = document.querySelectorAll('.face p img')
        for(let i=0; i<imm.length; i++){
            imm[i].style.filter = 'grayscale(100%)'
        }
        e.target.style.filter = 'none'
    }
    function LoadBnw(e) {
        if( e.target.id == faceIdx ){
            e.target.style.filter = 'none'
        }else{
            e.target.style.filter = 'grayscale(100%)'
        }
    }

    return(
        <div className="char" style={bgStyle}>
            <div>
                <div className="face">
                    {
                        charArr[chaIdx].map( (indi,k) => (
                            <p key={k} onClick={faceChange}>
                                <img src={require(`../img/long/${indi.face}`).default} alt="" id={k}
                                onLoad={LoadBnw} onClick={clickBnw} style={{ filter : "grayscale(100%)"}}/>
                            </p>
                        ))
                    }
                </div>
                <div className="ex">
                    {
                        <p>{chaNameArr[faceIdx]}</p>
                    }
                    {
                        <span dangerouslySetInnerHTML={{__html : chaDetailArr[faceIdx]}}></span>
                        
                    }
                    <figure>
                        {
                            imgArr[faceIdx].map( (v,k) => (
                                    <p key={k}>
                                        <img src={require(`../img/long/${v}`).default} alt=""/>
                                    </p>
                            ))

                        }
                    </figure> 
                </div>
            </div>
        </div>
    )
}
export default Character;