import React, { useState }from "react";
import '../App.css';
import Movie from '../json/movie.json';
import { Link } from "react-router-dom";


export default function ShortMov(){

    let [ name, setName ] = useState('');
    function movFil(e) {
        setName(e.target.value)
    }

    return(
        <div className="short">
            <div className="searchBar">
                <p>
                    <span/>
                    <input type="search" placeholder="Search" onChange={ movFil}/>
                </p>
            </div>
            <ul className="shortFil">
                {
                    Movie.Shorts.filter((val) => {
                            if( name == ""){
                                return val
                            }else if( val['movie'].toLowerCase().includes(name.toLowerCase()) ){
                                return val
                            }
                        }).map( (data, k) => (
                        <li key={k} >
                            <Link to={`/short/${data['movie']}`}>
                                <img src={require(`../img/short/${data['poster']}`).default} alt=""/>
                            </Link>
                            <span className="mvName">{data['movie']}</span>
                        </li>
                    ))
                }
            </ul>
        </div>
    )
}